import React from "react";

export default function VoterNominationView({
  numNoms,
  setNumNoms,
  numVoters,
  voterNoms,
  setVoterNoms,
  voterBGGUsernames,
  setVoterBGGUsernames,
  voterBGGGames,
  setVoterBGGGames,
  candidateList,
  setCandidateList,
  setStep,
  setCurrentVoter,
  bggGames,
  bggUsername,
  voterManual,
  setVoterManual,
  voterSelectedBGG,
  setVoterSelectedBGG
}) {
  let thisVoter = voterNoms.length;
  const manualValue = voterManual[thisVoter] || "";
  const selectedBGG = voterSelectedBGG[thisVoter] || [];
  const noms = manualValue
    .split(",")
    .map((s) => s.trim())
    .filter(Boolean)
    .slice(0, numNoms);

  return (
    <div
      style={{
        width: "100%",
        maxWidth: 480,
        margin: "0 auto",
        padding: "0 8px"
      }}
    >
      <h2>Voter Nomination Mode</h2>
      <label>
        Number of nominations per voter:
        <input
          type="number"
          min={1}
          value={numNoms}
          onChange={(e) => setNumNoms(Number(e.target.value))}
        />
      </label>
      <hr />
      <p>
        Voter {thisVoter + 1} of {numVoters}:<br />
        <b>Option 1: Select from imported BoardGameGeek collection</b>
        <br />
        {bggGames.length > 0 ? (
          <div>
            <p>Select up to {numNoms} games:</p>
            <div
              style={{
                maxHeight: 120,
                overflowY: "auto",
                border: "1px solid #ccc",
                padding: 8
              }}
            >
              {bggGames.map((game) => (
                <label key={game} style={{ display: "block" }}>
                  <input
                    type="checkbox"
                    checked={selectedBGG.includes(game)}
                    disabled={
                      !selectedBGG.includes(game) &&
                      selectedBGG.length >= numNoms
                    }
                    onChange={(e) => {
                      const updated = e.target.checked
                        ? [...selectedBGG, game]
                        : selectedBGG.filter((g) => g !== game);
                      setVoterSelectedBGG((prev) => {
                        const copy = [...prev];
                        copy[thisVoter] = updated;
                        return copy;
                      });
                    }}
                  />{" "}
                  {game}
                </label>
              ))}
            </div>
            <button
              style={{ marginTop: 6 }}
              disabled={selectedBGG.length !== numNoms}
              onClick={() => {
                setVoterNoms([...voterNoms, selectedBGG]);
                setVoterBGGUsernames([...voterBGGUsernames, bggUsername]);
                setVoterBGGGames([...voterBGGGames, bggGames]);
                setVoterManual((prev) => {
                  const copy = [...prev];
                  copy[thisVoter + 1] = "";
                  return copy;
                });
                setVoterSelectedBGG((prev) => {
                  const copy = [...prev];
                  copy[thisVoter + 1] = [];
                  return copy;
                });
                if (thisVoter + 1 === numVoters) {
                  const all = [].concat(...[...voterNoms, selectedBGG]);
                  setCandidateList([...new Set(all)]);
                  setStep("voter-entry");
                  setCurrentVoter(0);
                }
              }}
            >
              {thisVoter + 1 === numVoters ? "Finish" : "Next Voter"}
            </button>
          </div>
        ) : (
          <div style={{ color: "#888" }}>
            No BGG collection imported. Please go back and import a collection
            first.
          </div>
        )}
      </p>
      <hr />
      <b>Option 2: Enter manually</b>
      <br />
      <input
        style={{ width: "100%" }}
        value={manualValue}
        onChange={(e) => {
          const value = e.target.value;
          setVoterManual((prev) => {
            const copy = [...prev];
            copy[thisVoter] = value;
            return copy;
          });
        }}
        placeholder="e.g. Codenames, Splendor"
      />
      <button
        disabled={noms.length !== numNoms}
        onClick={() => {
          setVoterNoms([...voterNoms, noms]);
          setVoterBGGUsernames([...voterBGGUsernames, ""]);
          setVoterBGGGames([...voterBGGGames, []]);
          setVoterManual((prev) => {
            const copy = [...prev];
            copy[thisVoter + 1] = "";
            return copy;
          });
          setVoterSelectedBGG((prev) => {
            const copy = [...prev];
            copy[thisVoter + 1] = [];
            return copy;
          });
          if (thisVoter + 1 === numVoters) {
            const all = [].concat(...[...voterNoms, noms]);
            setCandidateList([...new Set(all)]);
            setStep("voter-entry");
            setCurrentVoter(0);
          }
        }}
        style={{ marginLeft: 8 }}
      >
        {thisVoter + 1 === numVoters ? "Finish" : "Next Voter"}
      </button>
    </div>
  );
}
