import { useState } from "react";

// Hook: Fetch BGG Collection
export function useBGGCollection(username) {
  const [games, setGames] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState("");

  async function fetchCollection() {
    setLoading(true);
    setError("");
    setGames([]);
    try {
      const response = await fetch(
        `https://boardgamegeek.com/xmlapi2/collection?username=${encodeURIComponent(
          username
        )}&own=1`
      );
      const xml = await response.text();
      if (xml.includes("<message>")) {
        setError(
          "Collection is being prepared by BoardGameGeek. Please wait and try again."
        );
        setLoading(false);
        return;
      }
      const parser = new window.DOMParser();
      const doc = parser.parseFromString(xml, "text/xml");
      const items = Array.from(doc.querySelectorAll("item"));
      const names = items.map((item) => item.querySelector("name").textContent);
      setGames(names);
    } catch (e) {
      setError("Failed to fetch collection.");
    }
    setLoading(false);
  }

  return { games, loading, error, fetchCollection };
}
