import React from "react";
import styled from "styled-components";
import { DEFAULT_ALGORITHM } from "../utils/rcvUtils";
import { DragDropContext, Droppable, Draggable } from "react-beautiful-dnd";

// Styled Components

const Container = styled.div`
  width: 100%;
  max-width: 480px;
  margin: 0 auto;
  padding: 0 8px;
  color: var(--color-text);
`;

const DroppableArea = styled.div`
  min-height: 120px;
  border: 1px solid var(--color-border);
  padding: 8px;
  border-radius: 8px;
  background: var(--color-surface);
  box-shadow: var(--shadow-light);
  transition: box-shadow var(--transition-fast);
`;

const DraggableItem = styled.div`
  padding: 12px;
  margin: 6px 0;
  background: var(--color-primary-light);
  border: 1px solid var(--color-border);
  border-radius: 6px;
  font-size: 1em;
  color: var(--color-text);
  box-shadow: var(--shadow-light);
  transition: box-shadow var(--transition-fast),
    background var(--transition-fast);
  &:hover {
    background: var(--color-primary);
    color: var(--color-surface);
    box-shadow: var(--shadow-medium);
  }
`;

const SubmitButton = styled.button`
  margin-top: 16px;
  width: 100%;
  min-height: 44px;
  font-size: 1em;
  background: var(--color-primary);
  color: var(--color-surface);
  border: none;
  border-radius: 6px;
  box-shadow: var(--shadow-light);
  cursor: pointer;
  transition: background var(--transition-fast),
    box-shadow var(--transition-fast);
  &:hover {
    background: var(--color-primary-dark);
    box-shadow: var(--shadow-medium);
  }
`;
export const StrictModeDroppable = ({ children, ...props }) => {
  const [enabled, setEnabled] = React.useState(false);

  React.useEffect(() => {
    const animation = requestAnimationFrame(() => setEnabled(true));

    return () => {
      cancelAnimationFrame(animation);
      setEnabled(false);
    };
  }, []);

  if (!enabled) {
    return null;
  }

  return <Droppable {...props}>{children}</Droppable>;
};
export default function VoterEntryView({
  currentVoter,
  numVoters,
  candidateList,
  dragList,
  setDragList,
  shuffleAndResetDragList,
  rankings,
  setRankings,
  setCurrentVoter,
  setStep,
  setResults,
  players = []
}) {
  // Ensure dragList is always an array
  const safeDragList = Array.isArray(dragList) ? dragList : [];
  const voterName = players[currentVoter]
    ? `${players[currentVoter]}'s`
    : `Voter ${currentVoter + 1}`;
  return (
    <Container>
      <h2>{voterName} Ranking</h2>
      <p>Drag to rank your choices (top = favorite):</p>
      <DragDropContext
        onDragEnd={(result) => {
          if (!result.destination) return;
          const items = Array.from(safeDragList);
          const [removed] = items.splice(result.source.index, 1);
          items.splice(result.destination.index, 0, removed);
          setDragList(items);
        }}
      >
        <StrictModeDroppable droppableId={`droppable-${currentVoter}`}>
          {(provided) => (
            <DroppableArea {...provided.droppableProps} ref={provided.innerRef}>
              {safeDragList.map((cand, idx) => (
                <Draggable key={cand} draggableId={cand} index={idx}>
                  {(provided, snapshot) => {
                    // Merge provided.draggableProps.style with styled-components
                    const style = {
                      ...provided.draggableProps.style,
                      // Optionally add visual feedback for dragging
                      boxShadow: snapshot.isDragging
                        ? "var(--shadow-medium)"
                        : "var(--shadow-light)",
                      zIndex: snapshot.isDragging ? 2 : 1
                    };
                    return (
                      <DraggableItem
                        key={cand}
                        draggableId={cand}
                        index={idx}
                        ref={provided.innerRef}
                        {...provided.draggableProps}
                        {...provided.dragHandleProps}
                        style={style}
                      >
                        {cand}
                      </DraggableItem>
                    );
                  }}
                </Draggable>
              ))}
              {provided.placeholder}
            </DroppableArea>
          )}
        </StrictModeDroppable>
      </DragDropContext>
      <SubmitButton
        onClick={() => {
          const newRankings = [...rankings, dragList];
          setRankings(newRankings);
          if (currentVoter + 1 === numVoters) {
            // Compute results and show them
            const results = DEFAULT_ALGORITHM(newRankings, candidateList);
            setResults(results);
            setStep("results");
          } else {
            setCurrentVoter(currentVoter + 1);
            shuffleAndResetDragList();
          }
        }}
      >
        {currentVoter + 1 === numVoters ? "Finish" : "Next Voter"}
      </SubmitButton>
    </Container>
  );
}
