// Clear all local cache (players, results, BGG)
export function clearAllCache() {
  localStorage.removeItem("rcv-players-list-v1");
  localStorage.removeItem("game-night-results-v1");
  localStorage.removeItem("bgg-candidates-cache-v1");
}
// BoardGameGeek import and cache helpers
const BGG_CACHE_KEY = "bgg-candidates-cache-v1";

// Import BGG collection for a username, cache, and return games array
export async function importBGGCollection(bggUsername) {
  if (!bggUsername) throw new Error("No username provided");
  try {
    const response = await fetch(
      `https://boardgamegeek.com/xmlapi2/collection?username=${encodeURIComponent(
        bggUsername
      )}&own=1`
    );
    const xml = await response.text();
    if (xml.includes("<message>")) {
      throw new Error(
        "Collection is being prepared by BoardGameGeek. Please wait and try again."
      );
    }
    const parser = new window.DOMParser();
    const doc = parser.parseFromString(xml, "text/xml");
    const items = Array.from(doc.querySelectorAll("item"));
    const games = items.map((item) => {
      const id = item.getAttribute("objectid");
      const name = item.querySelector("name")?.textContent || "";
      const image = item.querySelector("image")?.textContent || "";
      // yearpublished is not a direct child, so use getElementsByTagName
      const yearNode = item.getElementsByTagName("yearpublished")[0];
      const year = yearNode ? yearNode.textContent : "";
      const rank = item.querySelector("rank")?.textContent || "";
      const thumbnail = item.querySelector("thumbnail")?.textContent || "";
      return { id, name, image, year, rank, thumbnail };
    });
    localStorage.setItem(BGG_CACHE_KEY, JSON.stringify(games));
    return games;
  } catch (e) {
    throw e;
  }
}

// Player cache helpers
const PLAYERS_KEY = "rcv-players-list-v1";

// Get { last, all } players from localStorage
export function getPlayersCache() {
  try {
    const raw = localStorage.getItem(PLAYERS_KEY);
    if (raw) return JSON.parse(raw);
    return { last: [], all: [] };
  } catch {
    return { last: [], all: [] };
  }
}

// Set current and all players in localStorage
export function setPlayersCache(players) {
  try {
    const raw = localStorage.getItem(PLAYERS_KEY);
    let all = [];
    if (raw) {
      const parsed = JSON.parse(raw);
      all = parsed.all || [];
    }
    const updatedAll = Array.from(new Set([...players, ...all]));
    localStorage.setItem(
      PLAYERS_KEY,
      JSON.stringify({ last: players, all: updatedAll })
    );
    return updatedAll;
  } catch {
    return [];
  }
}
// Utility functions for saving and retrieving local history of game results
const RESULTS_KEY = "game-night-results-v1";
const MAX_HISTORY = 50;

// Save a new result entry (object: {winner, rounds, date, candidates})
export function saveResultEntry(result) {
  if (!result) return;
  let results = [];
  try {
    const raw = localStorage.getItem(RESULTS_KEY);
    if (raw) results = JSON.parse(raw);
  } catch {}
  // Add date if not present
  if (!result.date) result.date = new Date().toISOString();
  results.unshift(result);
  // Keep only last 50
  if (results.length > MAX_HISTORY) results = results.slice(0, MAX_HISTORY);
  localStorage.setItem(RESULTS_KEY, JSON.stringify(results));
}

// Get all past results (most recent first)
export function getPastResults() {
  try {
    const raw = localStorage.getItem(RESULTS_KEY);
    if (!raw) return [];
    return JSON.parse(raw);
  } catch {
    return [];
  }
}
