// Helper: RCV Algorithm (simplified)
export function calculateRCV(ballots, candidates) {
  let rounds = [];
  let active = [...candidates];
  let ballotCopies = ballots.map((b) => [...b]);
  let totalVotes = ballots.length;
  let winner = null;

  console.log({ ballots, candidates });

  while (!winner && active.length > 0) {
    let tally = {};
    active.forEach((c) => (tally[c] = 0));
    const currentActive = [...active];
    ballotCopies.forEach((b) => {
      let choice = b.find((c) => currentActive.includes(c));
      if (choice) tally[choice]++;
    });
    rounds.push({ ...tally });

    for (let c of active) {
      if (tally[c] > totalVotes / 2) {
        winner = c;
        break;
      }
    }
    if (winner) break;

    // Find candidate(s) with fewest votes
    let minVotes = Math.min(...active.map((c) => tally[c]));
    let toRemove = active.filter((c) => tally[c] === minVotes);
    active = active.filter((c) => !toRemove.includes(c));
  }
  return { winner, rounds };
}
export function weightedVoting(ballots, candidates) {
  let active = [...candidates];
  let ballotCopies = ballots.map((b) => [...b]);

  let tally = {};
  active.forEach((c) => (tally[c] = 0));
  ballotCopies.forEach((b) => {
    const vLength = b.length;
    b.forEach((c, i) => {
      if (active.includes(c)) {
        tally[c] += vLength - i;
      }
    });
  });

  return {
    winner: Object.keys(tally).reduce((a, b) => (tally[a] > tally[b] ? a : b)),
    rounds: [tally]
  };
}

export const DEFAULT_ALGORITHM = calculateRCV;
