import React from "react";
import styled from "styled-components";

const barColors = [
  "var(--color-primary)",
  "var(--color-accent-1)",
  "var(--color-success)",
  "var(--color-error)",
  "var(--color-warning)",
  "var(--color-accent-3)",
  "#a21caf",
  "var(--color-info)"
];

const Container = styled.div`
  flex-grow: 1;
  width: 100%;
`;

const WinnerTitle = styled.div`
  margin-bottom: 10px;
  letter-spacing: 1px;
  > h3 {
    width: 100%;
    display: inline-block;
    border-bottom: 1px solid var(--color-border);
  }
  > div {
    font-size: 20px;
    color: var(--color-success);
    font-weight: bold;
    margin-top: 20px;
    margin-bottom: 20px;
    display: flex;
    gap: 10px;
    align-items: center;
    > img {
      width: auto;
      height: 100%;
      max-width: 40%;
      max-height: 126px;
      object-fit: cover;
    }
  }
`;

const RoundBlock = styled.div`
  > strong {
    font-weight: 600;
    margin-bottom: 14px;
    font-size: 17px;
    color: var(--color-text);
  }
  > div {
    padding-left: 10px;
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
`;
const VotingDetails = styled.h3`
  margin-top: 40px;
  margin-bottom: 24px;
  border-bottom: 1px solid var(--color-border);
`;
const CandidateRow = styled.div`
  display: inline-flex;
  align-items: center;
  gap: 0;
  min-height: 30px;
`;

const CandidateName = styled.span`
  width: 110px;
  flex-grow: 0;
  flex-shrink: 0;
  font-weight: 500;
  color: ${({ isWinner }) =>
    isWinner ? "var(--color-success)" : "var(--color-text)"};
  font-size: 15px;
`;

const Bar = styled.div`
  height: 18px;
  min-width: 8px;
  border-radius: 6px;
  margin-right: 10px;
  transition: width 0.4s;
  box-shadow: var(--shadow-light);
  background: ${({ isWinner, color }) =>
    isWinner ? "var(--color-success)" : color};
`;

const VoteCount = styled.span`
  width: 32px;
  max-width: 32px;
  font-weight: 600;
  font-size: 15px;
  color: var(--color-text-muted);
  margin-left: 2px;
`;

export default function ResultsView({ results }) {
  // Store results in local history (top 50 by votes)
  React.useEffect(() => {
    if (!results) return;
    // Save the full result as a new entry
    import("../utils/localHistory").then((mod) => {
      mod.saveResultEntry({
        winner: results.winner,
        rounds: results.rounds,
        candidates: Object.keys(results.rounds[0] || {}),
        date: new Date().toISOString()
      });
    });
  }, [results]);

  const bggCache = React.useMemo(() => {
    // Try to get image/thumb from BGG cache
    let bggCache = [];
    try {
      const raw = localStorage.getItem("bgg-candidates-cache-v1");
      bggCache = raw ? JSON.parse(raw) : [];
    } catch {}

    const allNames = results ? Object.keys(results.rounds[0] || {}) : [];

    return allNames.reduce((acc, name) => {
      acc[name] = bggCache.find((e) => e.name === name) || {
        name,
        thumb: null
      };
      return acc;
    }, {});
  }, [results]);

  console.log({ bggCache, results });

  if (!results) return null;
  const { winner, rounds } = results;
  // Get all candidates from the first round
  const candidates = rounds.length > 0 ? Object.keys(rounds[0]) : [];
  // Find max votes in any round for scaling
  const maxVotes = Math.max(...rounds.flatMap((r) => Object.values(r)));

  return (
    <Container>
      <WinnerTitle>
        <h3>Winner</h3>
        <div>
          {bggCache[winner]?.thumbnail && (
            <img
              src={bggCache[winner]?.thumbnail}
              alt={bggCache[winner]?.name}
            />
          )}
          <span>{winner}</span>
        </div>
      </WinnerTitle>
      <div style={{ marginTop: 0 }}>
        <VotingDetails>Voting Details</VotingDetails>
        {rounds.map((round, i) => (
          <RoundBlock key={i} isLast={i === rounds.length - 1}>
            <strong>Round {i + 1}</strong>
            <div>
              {candidates.map((cand, idx) => {
                const votes = round[cand] || 0;
                const percent = maxVotes > 0 ? (votes / maxVotes) * 100 : 0;

                return !votes ? (
                  <></>
                ) : (
                  <CandidateRow key={cand}>
                    <CandidateName isWinner={cand === winner}>
                      {cand}
                    </CandidateName>
                    <Bar
                      isWinner={cand === winner}
                      color={barColors[idx % barColors.length]}
                      style={{ width: `${percent}%`, maxWidth: `${percent}%` }}
                    />
                    <VoteCount>{votes}</VoteCount>
                  </CandidateRow>
                );
              })}
            </div>
          </RoundBlock>
        ))}
      </div>
    </Container>
  );
}
